  // padding: 32px 0;
  img {
    width: 400px;
  }
}

.step {
  cursor: pointer;
  transition: all 0.3s;
  &.active {
    .number {
      color: $primary;
    }
    .description {
      color: $text-text-foreground;
    }
    background: $bg-primary-10;
  }
}
