<template>
  <div class="q-pa-md">
    <div class="flex justify-end q-gutter-sm q-mb-md">
      <q-btn
        color="secondary"
        to="/highlights"
      >
        <q-icon
          name="email"
          size="xs"
        />
        View highlights
      </q-btn>
      <q-btn
        @click="showwSettings = !showwSettings"
        color="secondary"
      >
        <q-icon
          name="settings"
          size="xs"
        />
        Settings
      </q-btn>
    </div>
    <div class="column">
      <div class="q-pb-sm">{{ readableDate }}</div>

      <q-btn color="secondary">
        <q-icon name="date_range" />
        Filter period

        <q-menu>
          <VueDatePicker
            inline
            model-auto
            range
            v-model="date"
            :enable-time-picker="false"
            @update:model-value="updateDate"
          />
        </q-menu>
      </q-btn>

      <q-intersection
        transition="slide-up"
        once
        v-for="(summaryItem, summaryIdx) in summaryItems"
        :key="summaryIdx"
      >
        <q-card class="summaryCard q-mt-md">
          <q-card-section>
            <div class="row justify-between items-center">
              <div class="title">
                {{ summaryItem.title }}
              </div>
              <q-icon :name="summaryItem.icon" />
            </div>
            <div v-if="summaryItem.showValue">
              <div
                class="text-primary heading-3 text-bold"
                :class="summaryItem.class"
              >
                {{ summaryItem.value }}
              </div>
              <div class="text-muted">
                {{ summaryItem.subtitle }}
              </div>
            </div>

            <div
              v-else
              class="text-bold"
            >
              Not enough data!
            </div>
          </q-card-section>
        </q-card>
      </q-intersection>
    </div>

    <q-dialog
      v-model="showwSettings"
      title="Participant settings"
    >
      <q-card class="settingsContent">
        <q-card-section class="row justify-between items-center full-width">
          <div class="heading-4">Participant settings</div>
          <q-icon
            name="close"
            size="md"
            @click="showwSettings = false"
          />
        </q-card-section>
        <q-card-section>
          <AuthorSettings :author-settings="authorSettings" />
        </q-card-section>
      </q-card>
    </q-dialog>
  </div>
</template>

<script setup lang="ts">
// TODO: Use store to show these cards, and make them dynamic. Top part should be static
import { ref, computed } from 'vue'

import { useStore } from '@/store'
import AuthorSettings from '@/components/dashboard/AuthorSettings.vue'
import dayjs from 'dayjs'
import VueDatePicker from '@vuepic/vue-datepicker'
import '@vuepic/vue-datepicker/dist/main.css'

const store = useStore()
const date = ref([])

const showwSettings = ref(false)
const authorSettings = computed(() => store.authorsSettings)
const readableDate = computed(() => date.value?.map((d) => dayjs(d).format('DD MMM, YYYY')).join(' - '))
const summaryItems = computed(() => store.summaryItems)

function updateDate(value: Date[] | null) {
  if (!value) return
  // date.value = value
  store.setFilterDate(value[0], value[1])
  console.log(value)
}
</script>

<style scoped lang="scss">
.title {
  font-weight: 500;
}
</style>
