<template>
  <div class="container">
    <!-- {{ data }} -->
    <highcharts :options="options" />
  </div>
</template>

<script setup lang="ts">
import { Author, Message } from '../utils/types.ts'
import type { PropType } from 'vue'
import { reactive, computed } from 'vue'
import { groupBy } from '../utils/helperFunctions.ts'

const props = defineProps({
  data: {
    type: Object as PropType<Author[]>,
    required: true,
  },

  isHour: {
    type: Boolean,
    default: false,
  },
})

const data = props.isHour ? Array.from({ length: 24 }, () => 0) : Array.from({ length: 7 }, () => 0)

function getHours() {
  return props.data.map((participant) => {
    const dates = getGroupedData(participant.messages)
    Object.entries(dates).forEach((item: [string | string, Message[]]) => {
      if (item[0] == 'null') {
        console.log(item[1]) // TODO: fix this
      }
      if (typeof item[0] === 'string') {
        data[parseInt(item[0], 10)] = item[1].length
      }
    })

    return {
      name: participant.name,
      data: data,
    }
  })
}

function getGroupedData(messages: Message[]) {
  return props.isHour
    ? groupBy(messages, 'hour')
    : groupBy(
        messages.map((item) => ({
          day: new Date(item.date)?.getDay(),
        })),
        'day'
      )
}

const options = reactive({
  title: null,
  chart: {
    type: 'column',
    zoomType: 'x',
    zooming: {
      type: 'x',
    },
  },
  series: computed(() => getHours()),
  xAxis: {
    type: '',

    title: {
      text: 'Date',
    },
  },
  yAxis: {
    title: {
      text: '',
    },
  },

  tooltip: {
    shared: true,
  },
})
</script>
